.About {
    width: 100%;
    height: calc(100vh - 70px);
    background-image: url("../../assets/home_projects.jpg");
    background-repeat: no-repeat;
    background-size: 100% 100%;
    animation: aboutPageAnim 0.5s 1 ease-in-out;
    display: flex;
}

@keyframes aboutPageAnim {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

@keyframes aboutInfoAnim {
    0% {
        height: 10%;
    }
    100% {
        height: calc(100% - 285px);
    }
}

@media only screen and (max-width: 600px) {

    .profileImg {
        width: 180px;
        height: 200px;
        border: 5px solid rgba(219, 219, 219, 0.8);
        border-radius: 50%;
        background-image: url("../../assets/profile.jpg");
        background-repeat: no-repeat;
        background-size: 101% 100%;
        position: absolute;
        left: 50px;
        top: 75px;
        z-index: 2;
    }
    /* .aboutInfo{
        width: 800px;
        height: 200px;
        border-top: 5px solid rgba(219, 219, 219, 0.8);
        border-right: 5px solid rgba(219, 219, 219, 0.8);
        border-bottom: 5px solid rgba(219, 219, 219, 0.8);
        border-radius: 20px 20px 20px 20px;
        position: absolute;
        left: 90px;
        top: 80px;
    } */
    .separator {
        width: 300px;
        height: calc(100% - 285px);
        /* border-right: 5px solid rgba(219, 219, 219, 0.4); */
        border-top: 5px solid rgba(219, 219, 219, 0.8);
        border-radius: 20px 20px 0px 0px;
        position: absolute;
        left: 0px;
        top: 280px;
        background-color: rgba(219, 219, 219, 0.2);
        animation: aboutInfoAnim 0.5s 1 ease-in-out;
    }
}
