.Projects{
    width: 100%;
    height: calc(100vh - 70px);
    background-image: url("../../assets/home_projects.jpg");
    background-size: 100% 100%;
    background-repeat: no-repeat;
}
.Projects .menu{
    position: absolute;
    left: 0px;
    top: 75px;
    z-index: 20;
}





.content div{
    width: 180px;
    height: 40px;
    background-color: red;
    color:black;
    font-size: 18px;
    border: 2px solid black;
    list-style: none;
    margin-bottom: 5px;
    margin-left: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 2px;
    animation: navPanelItemAnims 0.35s 1 ease-in;
}






@keyframes navPanelAnims {
    0% {height: 10%;}
    100% {height: 100%;}
}

@keyframes navPanelItemAnims {
    0% {opacity: 0; height: 10px; font-size: 0px;}
    100% {opacity: 1; height: 40px; font-size: 18px;}
}


@media only screen and (max-width: 600px) {

    .Projects .NavPanel{
        width:200px;
        height: calc(100vh - 70px);
        background-color: aliceblue;
        border-right: 2px solid rgba(0,0,0,0.6);
        position: absolute;
        left: 0px;
        top: 70px;
        z-index: 2;
        display: none;
        animation: navPanelAnims 0.3s 1 ease-in-out; 
    }

    .Projects .NavPanel .content{
        width: inherit;
        position: sticky;
        top: 110px;
        left: 0px;
    }
    .content div:hover{
        transform: scale(1.02);
        margin-top: 5px;
        margin-bottom: 5px;
        margin-left: 10px;
        transition: 0.25s;
        border-color: #dbdbdb;
    }

    .Projects .showNavPanel{
        display: block;
    }

    .Projects .projectDescription{
        width:calc(100% - 0px);
        height: calc(100vh - 110px);
        position: absolute;
        left: 0px;
        top: 110px;
        overflow-y: scroll;

    }

}
@media only screen and (min-width: 600px) {
    .Projects .NavPanel{
        width:200px;
        height: calc(100vh - 70px);
        background-color: aliceblue;
        border-right: 2px solid rgba(0,0,0,0.6);
        position: absolute;
        left: 0px;
        top: 70px;
        z-index: 2;
        animation: navPanelAnims 0.3s 1 ease-in-out; 
    }
    .Projects .NavPanel .content{
        width: inherit;
        position: sticky;
        top: 80px;
        left: 0px;
    }

    .content div:hover{
        transform: scale(1.1);
        margin-top: 10px;
        margin-bottom: 10px;
        margin-left: 20px;
        transition: 0.25s;
    }
    .Projects .menu{
        display: none;
    }
    .Projects .projectDescription{
        width:calc(100% - 200px);
        height: calc(100vh - 70px);
        position: absolute;
        left: 200px;
        top: 70px;
        overflow-y: scroll;
    }
}