.betPanel{
    /* width: calc(100vh - 80px); */
    width: fit-content;
    height: 90px;
    /* background-color: seagreen; */
    /* justify-content: center;
    align-items: center; */
}

.scroll-items{
    list-style:none;
    display: flex;
    margin: 0%;
    padding-top: 10px;
    padding-right: 20px;
    

}
.scroll-items li{
    width:50px;
    height: 70px;
    background-color: grey;
    
    border: 5px transparent;
    /* border-right: 5px transparent; */
    display: flex;          /* follow these 3 lines of code to center the items */
    justify-content: center;
    align-items: center;
    flex-shrink: 0;  /* default value is '1', means items shrink to fit in the parent container */
} 

.scroll-items li img{
    width: 40px;
    height: 40px;
}

.betChip{
    width: 40px;
    height: 40px;
    background-image: url("../../../../../assets/funCard/chips/chip_5.png");
    background-size: 100% 100%;
    background-repeat: no-repeat;
}

