.plinko_stage input{
    position: absolute;
    left:10px;
    top: 20px;
}
.plinko_stage .start{
    position: absolute;
    left:10px;
    top: 50px;
}
.plinko_stage .close{
    position: absolute;
    right:10px;
    top: 50px;
}